	module.exports = {"data":{"site":{"siteMetadata":{"docSections":{"basics":["/basics/introduction/","/basics/quick-start-guide/","/basics/slow-start-guide/","/basics/guide-react/","/basics/guide-vue/","/basics/guide-angular/","/basics/writing-stories/","/basics/exporting-storybook/","/basics/faq/","/basics/community/","/basics/live-examples/"],"configurations":["/configurations/default-config/","/configurations/custom-webpack-config/","/configurations/custom-babel-config/","/configurations/add-custom-head-tags/","/configurations/serving-static-files/","/configurations/env-vars/","/configurations/cli-options/"],"testing":["/testing/react-ui-testing/","/testing/structural-testing/","/testing/interaction-testing/","/testing/css-style-testing/","/testing/manual-testing/"],"addons":["/addons/introduction/","/addons/using-addons/","/addons/addon-gallery/","/addons/writing-addons/","/addons/api/"]}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/404/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/addons/addon-gallery/"},"frontmatter":{"title":"Addon Gallery","id":"addon-gallery"}}},{"node":{"fields":{"slug":"/addons/api/"},"frontmatter":{"title":"API","id":"api"}}},{"node":{"fields":{"slug":"/addons/introduction/"},"frontmatter":{"title":"Intro to Addons","id":"introduction"}}},{"node":{"fields":{"slug":"/addons/using-addons/"},"frontmatter":{"title":"Using Addons","id":"using-addons"}}},{"node":{"fields":{"slug":"/addons/writing-addons/"},"frontmatter":{"title":"Writing Addons","id":"writing-addons"}}},{"node":{"fields":{"slug":"/configurations/add-custom-head-tags/"},"frontmatter":{"title":"Add Custom Head Tags","id":"add-custom-head-tags"}}},{"node":{"fields":{"slug":"/configurations/cli-options/"},"frontmatter":{"title":"CLI Options","id":"cli-options"}}},{"node":{"fields":{"slug":"/configurations/custom-babel-config/"},"frontmatter":{"title":"Custom Babel Config","id":"custom-babel-config"}}},{"node":{"fields":{"slug":"/configurations/custom-webpack-config/"},"frontmatter":{"title":"Custom Webpack Config","id":"custom-webpack-config"}}},{"node":{"fields":{"slug":"/configurations/default-config/"},"frontmatter":{"title":"Default Config","id":"default-config"}}},{"node":{"fields":{"slug":"/configurations/env-vars/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/configurations/serving-static-files/"},"frontmatter":{"title":"Serving Static Files","id":"serving-static-files"}}},{"node":{"fields":{"slug":"/basics/community/"},"frontmatter":{"title":"Community","id":"community"}}},{"node":{"fields":{"slug":"/basics/exporting-storybook/"},"frontmatter":{"title":"Exporting Storybook as a Static App","id":"exporting-storybook"}}},{"node":{"fields":{"slug":"/basics/faq/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/basics/guide-angular/"},"frontmatter":{"title":"Storybook for Angular","id":"guide-angular"}}},{"node":{"fields":{"slug":"/basics/guide-react/"},"frontmatter":{"title":"Storybook for React","id":"guide-react"}}},{"node":{"fields":{"slug":"/basics/guide-vue/"},"frontmatter":{"title":"Storybook for Vue","id":"guide-vue"}}},{"node":{"fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"Introduction","id":"introduction"}}},{"node":{"fields":{"slug":"/basics/live-examples/"},"frontmatter":{"title":"Live Examples","id":"live-examples"}}},{"node":{"fields":{"slug":"/basics/quick-start-guide/"},"frontmatter":{"title":"Quick Start Guide","id":"quick-start-guide"}}},{"node":{"fields":{"slug":"/basics/slow-start-guide/"},"frontmatter":{"title":"Slow start guide","id":"slow-start-guide"}}},{"node":{"fields":{"slug":"/basics/writing-stories/"},"frontmatter":{"title":"Writing Stories","id":"writing-stories"}}},{"node":{"fields":{"slug":"/testing/css-style-testing/"},"frontmatter":{"title":"CSS/Style Testing","id":"css-style-testing"}}},{"node":{"fields":{"slug":"/testing/interaction-testing/"},"frontmatter":{"title":"Interaction Testing","id":"interaction-testing"}}},{"node":{"fields":{"slug":"/testing/manual-testing/"},"frontmatter":{"title":"Manual Testing","id":"manual-testing"}}},{"node":{"fields":{"slug":"/testing/react-ui-testing/"},"frontmatter":{"title":"Introduction: React UI Testing","id":"react-ui-testing"}}},{"node":{"fields":{"slug":"/testing/structural-testing/"},"frontmatter":{"title":"Structural Testing","id":"structural-testing"}}}]},"markdownRemark":{"html":"<hr>\n<p>id: ‘faq’</p>\n<h2 id=\"title-frequently-asked-questions\"><a href=\"#title-frequently-asked-questions\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>title: ‘Frequently Asked Questions’</h2>\n<p>Here are some answers to frequently asked questions. If you have a question, you can ask it by opening an issue on the <a href=\"https://github.com/storybooks/storybook/\">Storybook Repository</a>.</p>\n<h3 id=\"how-can-i-run-coverage-tests-with-create-react-app-and-leave-out-stories\"><a href=\"#how-can-i-run-coverage-tests-with-create-react-app-and-leave-out-stories\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>How can I run coverage tests with Create React App and leave out stories?</h3>\n<p>Create React App does not allow providing options to Jest in your <code>package.json</code>, however you can run <code>jest</code> with commandline arguments:</p>\n<pre><code class=\"language-sh\">npm test -- --coverage --collectCoverageFrom='[\"src/**/*.{js,jsx}\",\"!src/**/stories/*\"]'\n</code></pre>\n<h3 id=\"i-see-referenceerror-react-is-not-defined-when-using-storybooks-with-nextjs\"><a href=\"#i-see-referenceerror-react-is-not-defined-when-using-storybooks-with-nextjs\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>I see <code>ReferenceError: React is not defined</code> when using storybooks with Next.js</h3>\n<p>Next automatically defines <code>React</code> for all of your files via a babel plugin. You must define <code>React</code> for JSX to work. You can solve this either by:</p>\n<ol>\n<li>Adding <code>import React from 'react'</code> to your component files.</li>\n<li>Adding a <code>.babelrc</code> that includes <a href=\"https://www.npmjs.com/package/babel-plugin-react-require\"><code>babel-plugin-react-require</code></a></li>\n</ol>\n<h3 id=\"how-do-i-setup-storybook-to-share-webpack-configuration-with-nextjs\"><a href=\"#how-do-i-setup-storybook-to-share-webpack-configuration-with-nextjs\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>How do I setup Storybook to share Webpack configuration with Next.js?</h3>\n<p>You can generally reuse webpack rules fairly easily by placing them in a file that is <code>require()</code>-ed from both your <code>next.config.js</code> and your <code>.storybook/webpack.config.js</code> files. For example, <a href=\"https://gist.github.com/metasean/cadd2becd60cc3b295bf49895a56f9b4\">this gist</a> sets both next.js and storybook up with global stylesheets.</p>\n<h3 id=\"why-is-there-no-addons-channel\"><a href=\"#why-is-there-no-addons-channel\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Why is there no addons channel?</h3>\n<p>A common error is that an addon tries to access the “channel”, but the channel is not set. This can happen in a few different cases:</p>\n<ol>\n<li>In storybook/addon development, it can be an NPM version problem. If there are two versions of the <code>@storybook/addons</code> NPM package, it will cause problems. In this case, make sure there is only a single version of <code>@storybook/addons</code> being used by your project.</li>\n<li>In React Native, it’s a special case that’s documented in <a href=\"https://github.com/storybooks/storybook/issues/1192\">#1192</a></li>\n</ol>\n<h3 id=\"can-i-modify-react-component-state-in-stories\"><a href=\"#can-i-modify-react-component-state-in-stories\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Can I modify React component state in stories?</h3>\n<p>Not directly. If you control the component source, you can do something like this:</p>\n<pre><code class=\"language-js\">import React, { Component} from 'react';\nimport { storiesOf } from '@storybook/react';\n\nclass MyComponent extends Component {\n  constructor(props) {\n    super(props)\n    this.setState({\n      someVar: 'defaultValue',\n      ...props.initialState\n    })\n  }\n  // ...\n}\n\nstoriesOf('MyComponent', module)\n  .add('default', () => &#x3C;MyComponent />)\n  .add('other', () => &#x3C;MyComponent initialState={{ someVar: 'otherVal' }});\n</code></pre>","fields":{"slug":"/basics/faq/"},"frontmatter":{"title":"","id":null}}},"pathContext":{"slug":"/basics/faq/"}}

